.topbar {
  display: flex;
  position: fixed;
  width: 100%;
  justify-content: space-between;
  height: 60px;
  background-color: #32393d;
  padding: 0 15px; }

.logo {
  font-size: 2.8rem;
  color: white;
  line-height: 60px;
  margin: 0 15px; }

.search {
  font-size: 16px;
  margin: 12px 0;
  background-color: #1e2024;
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  width: 30rem;
  transition: 0.2s all;
  border: 1.5px solid transparent; }
  .search:focus {
    outline: none;
    border: 1.5px solid #ff9a60;
    width: 50rem; }

.isearch {
  margin: 10px;
  height: 40px;
  width: 80px;
  background-color: #ff9a60;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  z-index: 10;
  cursor: pointer;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.5);
  transition: 0.2s all; }
  .isearch:hover {
    background-color: #ffaa7a;
    box-shadow: 0 4px 5px rgba(0, 0, 0, 0.5); }
  .isearch i {
    position: relative;
    transition: 0.2s all;
    font-size: 2.8rem;
    color: white;
    z-index: 11; }
